img {
  display: block;
}

body.dark .shopping-empty-text {
  color: rgba(255, 255, 255, .6);
}

.shopping {
  box-sizing: border-box;
  width: 100%;
}

.shopping-title {
  letter-spacing: -1.28px;
  margin-bottom: 40px;
  font-family: DM Sans;
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

.shopping-blue {
  color: var(--accent-color);
}

.shopping-empty-list {
  flex-direction: column;
  align-items: center;
  max-width: 265px;
  max-height: 248px;
  margin: 120px auto 302px;
  display: flex;
}

.shopping-empty-text {
  text-align: center;
  letter-spacing: -.28px;
  width: 236px;
  margin-bottom: 14px;
  font-family: DM Sans;
  font-size: 14px;
  line-height: 18px;
}

.shopping-book-list {
  margin-bottom: 40px;
}

.shopping-book-item {
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  min-width: 335px;
  height: 170px;
  margin: auto;
  padding: 12px;
  display: flex;
}

.shopping-book-item:not(:last-child) {
  margin-bottom: 20px;
}

.shopping-book-img {
  border-radius: 8px;
  min-width: 100px;
  height: 142px;
  margin-right: 14px;
  overflow: hidden;
}

.shopping-book-poster {
  outline: 1px solid #000;
  width: 100%;
  height: 100%;
}

.shopping-book-text {
  flex-direction: column;
  min-width: 193px;
  display: flex;
  position: relative;
}

.shopping-book-title {
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.shopping-book-genres {
  color: var(--paragraf-color);
  letter-spacing: .36px;
  margin-bottom: 8px;
  font-family: DM Sans;
  font-size: 12px;
  line-height: 14px;
}

.shopping-book-description {
  letter-spacing: -.56px;
  height: 68px;
  margin-bottom: 14px;
  font-family: DM Sans;
  font-size: 14px;
  line-height: 18px;
  overflow: hidden;
}

.shopping-book-item-footer {
  justify-content: space-between;
  height: 16px;
  display: flex;
}

.shopping-book-author {
  color: var(--paragraf-color);
  letter-spacing: -.48px;
  align-items: flex-end;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  line-height: 14px;
  display: flex;
}

.shopping-book-shops {
  align-items: center;
  gap: 8px;
  width: 80px;
  height: 16px;
  list-style: none;
  display: flex;
}

.shopping-shop-link {
  display: block;
}

.shopping-svg-amazon {
  border-radius: 0;
  width: 32px;
  height: 11px;
}

.shopping-svg-open-book {
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.shopping-svg-book-shop {
  width: 16px;
  height: 16px;
}

.shopping-btn-delete {
  background-color: var(--accent-color);
  cursor: pointer;
  border-color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 28px;
  height: 28px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 0;
  right: 0;
}

.svg-trash {
  fill: var(--accent-color);
  pointer-events: none;
}

.svg {
  min-width: 16px;
  height: 16px;
  display: block;
}

.display {
  display: none;
}

.img-thumb {
  position: relative;
}

.img-thumb:hover:after {
  content: "";
  opacity: .3;
  filter: blur(6.5914px);
  background: #000;
  border-radius: 50%;
  width: 100%;
  height: 38%;
  display: block;
  position: absolute;
  bottom: .01%;
}

.img-thumb:before {
  opacity: 0;
}

@media screen and (min-width: 768px) {
  .shopping-title {
    letter-spacing: -1.92px;
    font-size: 48px;
    line-height: 52px;
  }

  .shopping-empty-list {
    max-width: 349px;
    max-height: 309px;
    margin: 140px auto 394px;
  }

  .shopping-empty-text {
    letter-spacing: -.36px;
    width: 349px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 24px;
  }

  .shopping-empty-img {
    width: 322px;
    height: 241px;
  }

  .shopping-book-item {
    min-width: 704px;
    height: 213px;
    padding: 22px;
  }

  .shopping-book-img {
    min-width: 116px;
    height: 165px;
    margin-right: 24px;
  }

  .shopping-book-text {
    width: 100%;
  }

  .shopping-book-genres {
    margin-bottom: 14px;
  }

  .shopping-book-description {
    height: 53px;
    margin-bottom: 32px;
  }

  .shopping-book-item-footer {
    height: 30px;
  }

  .shopping-book-shops {
    gap: 16px;
    width: 140px;
    height: 30px;
  }

  .shopping-shop-link {
    align-items: center;
    display: flex;
  }

  .shopping-svg-amazon {
    width: 48px;
    height: 15px;
  }

  .shopping-svg-open-book {
    width: 28px;
    height: 27px;
  }

  .shopping-svg-book-shop {
    width: 32px;
    height: 30px;
  }

  .shopping-btn-delete {
    width: 34px;
    height: 34px;
  }
}

@media screen and (min-width: 1440px) {
  .shopping-book-item {
    width: 996px;
  }

  .shopping-empty-list {
    margin-bottom: 157px;
  }
}

